import React, { useState, useEffect } from "react";

import "./fourier.scss";

export const Fourier = (props: { src: string }) => {
  const [numberOfCircles, setNumberOfCircles] = useState(1);

  const { src } = props;

  useEffect(() => {
    fetch(src)
      .then(res => res.text())
      .then(console.log)
  }, [])

  return (
    <div className="fourier">
      <div className="controls">
        <label>
          Circles: {`${numberOfCircles}`.padStart(2, '0')}
          <input
            type="range"
            min="1" max="50"
            value={numberOfCircles}
            onChange={e => setNumberOfCircles(e.target.valueAsNumber)}  />
        </label>
      </div>
      <figure className="f-of-t">

      </figure>
      <div className="output">

      </div>
    </div>
  );
};
